@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap');

.peopleContainer {
    font-family: 'JetBrains Mono', monospace;
    padding: 2rem 1.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 30%);
    border-radius: 1rem;
    border: 10px double black;
    background-color: white;
    width: 450px;
    vertical-align: center;
}

.peopleContainer button {
    font-family: 'JetBrains Mono', monospace;
    background-color: rgb(255, 74, 19);
    font-weight: 800;
}

.peopleContainer button:hover {
    background-color: rgb(240, 81, 32);
    transition: all ease 400ms;
}

.peopleContainer span {
    user-select: none;
    cursor: pointer;
    position: absolute;
}

.xContainer {
    position: relative;
    width: 100%;
    text-align: center;
    justify-content: flex-start;
    display: flex;
    align-items: center;
}