@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap');
.container {
    font-family: 'JetBrains Mono', monospace;
    font-weight: 800;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,30%);
    background-color: rgb(250, 94, 83);
    padding: 4rem 2rem;
    border-radius: 1rem;
    border: 5px double black;
    box-shadow: 8px 10px 3px 0 rgb(255,255,255),12px 17px 0px 0 rgb(149, 24, 15), 0 5px 50px 10px rgb(0,0,0) ;
    user-select: none;
}

.container button {
    font-family: 'JetBrains Mono', monospace;
    padding: 1rem;
    border-radius: 1.2rem;
    background-color: rgb(255, 226, 62);
    font-weight: 800;
}

.container h1 {
    background-color: rgb(255, 226, 62);
    padding: 0.8rem;
    margin-bottom: 1.4rem;
    border-radius: 1.2rem;
    cursor: pointer;
}

.container input {
    font-family: 'JetBrains Mono', monospace;
}